import { createWebHistory , createRouter  } from 'vue-router'
import indexGuide from '@/views/indexGuide.vue'
import DArending from '@/views/DA/DArending.vue'

// 라우터 설계
const routes = [
    { 
      path : '/',
      name: '/',
      component: indexGuide,
    },{ 
      path : '/indexGuide',
      name: '/indexGuide',
      component: indexGuide,
    },
    { 
      path : '/DArending',
      name: '/DArending',
      component: DArending,
    }
]
export const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

